@font-face {
  font-weight: 400;
  font-family: "Oswald";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-weight: 500;
  font-family: "Oswald";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
.btn {
  display: block;
  padding: 11px 0;
  font: inherit;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  outline: thin;
}
.btn:hover {
  background-color: #5eaa2f;
}
.btn:active,
.btn:active {
  color: #8fc46e;
  background-color: #5eaa2f;
}
@media (min-width: 768px) {
  .btn {
    padding: 15px 0;
  }
}
.catalog {
  margin-bottom: 26px;
  background-color: #ffffff;
}
.catalog__wrapper {
  width: 320px;
  padding: 0;
  margin: 0 auto;
}
.catalog__title {
  margin: 0;
  margin-bottom: 28px;
  padding: 14px 20px 0;
  box-sizing: border-box;
  font-size: 36px;
  line-height: 60px;
  font-weight: 400;
  color: #111111;
  text-align: left;
  border-top: 1px solid #ededed;
}
.catalog__list {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  border: 1px solid #ededed;
}
.catalog__item {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
  min-height: 208px;
  box-sizing: border-box;
  border-top: 1px solid #ededed;
}
.catalog__item:first-child {
  border-top: none;
}
.catalog__item-show {
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: none;
}
@media (min-width: 768px) {
  .catalog {
    margin-bottom: 18px;
  }
  .catalog__wrapper {
    width: 708px;
    padding: 0 30px;
  }
  .catalog__title {
    min-height: 65px;
    margin: 0;
    margin-bottom: 70px;
    padding: 0;
    font-size: 60px;
    line-height: 60px;
    font-weight: 400;
    border: none;
  }
  .catalog__list {
    display: flex;
    flex-wrap: wrap;
    width: 708px;
    border: none;
  }
  .catalog__item {
    position: relative;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: flex-start;
    width: 324px;
    min-height: 434px;
    margin: 90px 60px 48px 0;
    padding: 165px 20px 17px 20px;
    box-sizing: border-box;
    border: none;
    background: linear-gradient(to bottom, #f2f2f2 434px, #f2f2f2 434px, #f2f2f2 434px, #f2f2f2 434px);
  }
  .catalog__item:nth-child(2n) {
    margin-right: 0;
  }
  .catalog__item-show {
    padding: 53px 19px 40px 19px;
    background: none;
    border: 1px solid #cdcdcd;
  }
}
@media (min-width: 1300px) {
  .catalog__wrapper {
    width: 1220px;
    padding: 0 40px;
  }
  .catalog__list {
    width: 1220px;
  }
  .catalog__item {
    width: 245px;
    min-height: 434;
    margin-right: 80px;
  }
  .catalog__item:nth-child(2n) {
    margin-right: 80px;
  }
  .catalog__item:nth-child(4n) {
    margin-right: 0;
  }
}
.copyright__link {
