  }
}
.features__item--drink::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 60px;
  height: 60px;
  background: url("../img/advantage-powder@1x.png") no-repeat;
  background-color: #68b738;
  background-position: center;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .features__item--drink::before {
