}
@media (min-width: 768px) {
  .features {
    margin-bottom: 20px;
  }
  .features__wrapper {
    width: 708px;
    padding: 0 30px;
  }
  .features__title {
    margin: 0;
    margin-bottom: 6px;
    font-size: 60px;
    font-weight: 400;
    line-height: 60px;
  }
  .features__list {
    display: flex;
    flex-wrap: wrap;
  }
  .features__item {
    width: 324px;
    min-height: 185px;
    margin-top: 64px;
    margin-right: 60px;
    margin-bottom: 68px;
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-between;
  }
  .features__item:nth-child(2n) {
    margin-right: 0;
  }
  .features__item--leaf::before,
  .features__item--drink::before,
  .features__item--eating::before,
  .features__item--clock::before {
    width: 80px;
    height: 80px;
  }
  .features__item--leaf::after,
  .features__item--drink::after,
  .features__item--eating::after,
  .features__item--clock::after {
    content: "1";
