    z-index: -1;
    left: 118px;
    top: -38px;
    width: 70px;
    height: 230px;
    font-family: "Oswald", "Arial", sans-serif;
    font-weight: 400;
    font-size: 281px;
    line-height: 281px;
    color: #f2f2f2;
  }
  .features__item--drink::after {
    content: "2";
    left: 112px;
    top: -40px;
  }
  .features__item--eating::after {
    content: "3";
    left: 112px;
    top: -37px;
  }
  .features__item--clock::after {
    content: "4";
    left: 112px;
    top: -36px;
  }
  .features__text {
    width: 265px;
    padding-left: 0;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
  }
